/*1.定义全局的请求成功或者错误的提示
* 2.将axios请求封装成全局函数，课随时调用方便书写
* */

import axios from 'axios'  //引入请求的封装工具

import { Message } from 'element-ui';
import router from "../router";
//全局引用消息提示

/*后端返回的数据在此先处理一次*/
axios.interceptors.response.use(success=>{//注意success数据的格式层次
    //返回成功也可能是业务逻辑中的错误，不是请求本身失败的错误，也就是实际请求已经成功
    if(success.status&&success.status==200 &&success.data.status==500){
        Message.error({message:success.data.msg});
        return ;
    }
    if(success.data.msg){
        Message.success({message:success.data.msg})
    }
    return success.data;
},error => {
    if(error.response.status==504 || error.response.status==404){
        Message.error({message:'服务器被吃了...'});
    }else if(error.response.status==403){
        Message.error({message:'权限不足，请联系管理员'});
    }else if(error.response.status==401){
        Message.error({message:'尚未登录，请登录'});
        router.replace("/");
    }else {
        if(error.response.data.msg){
            Message.error({message:error.response.data.msg})
        }else{
            Message.error({message:'未知错误'})
        }
    }
    return;
});

//封装请求
let base=''; //请求前缀

//封装登录的请求，SpringSecurity只支持keyValue的形式
export const postKeyValueRequest=(url,params)=>{
    return axios({
        method:'post',
        url: `${base}${url}`,
        data:params,
        transformRequest:[function (data) {//将json格式的data转化为url的形式
            let ret='';
            for(let i in data){
                ret+=encodeURIComponent(i)+'='+encodeURIComponent(data[i])+'&'//url请求格式的转化
            }
            return ret;
        }],
        headers:{
            'Content-Type':'application/x-www-form-urlencoded'//定义请求头
        }
    });
};

//封装 post等请求
export const postRequest=(url,params)=>{
    return axios({
        method:'post',
        url:`${base}${url}`,
        data: params
    })
}

export const getRequest=(url,params)=>{
    return axios({
        method:'get',
        url:`${base}${url}`,
        data: params
    })
}

export const putRequest=(url,params)=>{
    return axios({
        method:'put',
        url:`${base}${url}`,
        data: params
    })
}

export const deleteRequest=(url,params)=>{
    return axios({
        method:'delete',
        url:`${base}${url}`,
        data: params
    })
}


