<template>
    <!--登录页面绘制-->
    <div>
        <el-form
                ref="loginForm"
                :rules="rules"
                :model="loginForm"
                class="loginContainer">
            <h3 class="loginTitle">系统登录</h3>
            <el-form-item label="用户名" prop="username">
                <el-input size="normal" type="text" v-model="loginForm.username" placeholder="请输入用户名"
                          auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input size="normal" type="password" v-model="loginForm.password" placeholder="请输入密码"
                          auto-complete="off" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox>
            <el-button type="primary" style="width: 100%" @click="submitLogin">登录</el-button>

        </el-form>
    </div>
</template>

<script>

    export default {
        name: "Login",
        data() {
            return {
                checked: true,
                loginForm: {
                    username: 'admin',/*默认值*/
                    password: '123'
                },
                rules: {
                    /*每一项输入的验证，是一个数组，可以有多重验证*/
                    username: [{required: true, message: "请输入用户名", trigger: 'blur'}],
                    password: [{required: true, message: "请输入密码", trigger: 'blur'}]
                }
            }
        },
        methods:{
            submitLogin(){
                this.$refs.loginForm.validate((valid)=>{
                    if(valid){
                        //此处的/doLogin请求发起后直接交给SpringConfig中的loginProcessingUrl来处理，进行安全配置
                       this.postKeyValueRequest('/doLogin',this.loginForm).then(resp=>{
                               if(resp){//登录成功后，将获得的User(Hr)存储到sessionStorage中
                                   window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                                   //登录成功则跳转到home页，可以跳转的前提是home页在router里面注册home页
                                    let path = this.$route.query.redirect;
                                    this.$router.replace((path=='/' || path==undefined)?'/home':path);
                               }
                       })
                    }else {
                        this.$message.error('请输入所有字段');
                        return false;
                    }
                });
            },
        }
    }
</script>

<style>
    .loginContainer {
        margin: 180px auto;
        width: 350px;
        background-clip: border-box;
        background: #fff;
        border-radius: 15px;
        border: 1px solid #eeeeee;
        padding: 20px 20px 20px 20px;
        box-shadow: 0 0 20px #777777;
    }

    .loginRemember {
        text-align: left;
        padding: 0px 0px 20px 0px;
    }

    .loginTitle{
        margin: 15px auto 20px auto;
        text-align: center;
    }
</style>