import Vue from 'vue'
import App from './App.vue'
import store from './store'
/*引入element-ui库*/
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
/*引入图标库*/
import 'font-awesome/css/font-awesome.min.css'

/*将封装的全局方法制作成Vue全局插件*/
import {postKeyValueRequest} from "./utils/api";
import {postRequest} from "./utils/api";
import {putRequest} from "./utils/api";
import {getRequest} from "./utils/api";
import {deleteRequest} from "./utils/api";

//将全局方法添加到prototype上
Vue.prototype.postKeyValueRequest = postKeyValueRequest;  //创建方法的引用
Vue.prototype.putRequest = putRequest;
Vue.prototype.postRequest = postRequest;
Vue.prototype.getRequest = getRequest;
Vue.prototype.deleteRequest = deleteRequest;


import router from './router'
import Router from 'vue-router'

/*解决页面跳转，控制台报错的*/
const originalPush = Router.prototype.push
Router.prototype.push = function push(location, onResolve, onReject) {
    if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
    return originalPush.call(this, location).catch(err => err)
}

import {initMenu} from "./utils/menus";

Vue.use(ElementUI); /*引入element-ui库*/

Vue.config.productionTip = false

//全局导航守卫：检测页面跳转信息,并根据跳转信息进行页面初始化的跳转，以防止用户点击"“f5”刷新时，之前initMenu的函数无法再执行
router.beforeEach((to, from, next) => {
    // console.log(to);
    // console.log(from);
    if (to.path == '/') {
        next(); //类似于后端中的filter链，供页面继续向下执行
    } else {
        //判断用户是否登录
        if (window.sessionStorage.getItem("user")) {//如果用户已登录
            initMenu(router, store);
            next();
        } else {
            next('?/redirect=' + to.path); //记录登录成功后想要跳转的界面
        }
    }

})


/*页面主入口*/
new Vue({
    router,
    store,//引入状态管理
    render: h => h(App)
}).$mount('#app')
