import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
/*引入vue的状态管理工具,使在多个页面中共享数据，同时数据也是响应式的
* 别忘了在main.js中引入
* */
const store = new Vuex.Store({
    state:{
        routes:[],
    },
    mutations:{
        initRoutes(state,data){
            state.routes=data;//此处的data就是格式化好后的fmRoutes
        }
    },
    actions:{

    }

})

export default store;