import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "../views/Login";
import Home from "../views/Home";



Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Login',
        component: Login,
        hidden:true //该路由信息是否隐藏的标记
    },

    {
        path: '/home', //请求路径
        name: 'Home',  //该route名称
        component: Home,  //映射的组件
        hidden:false,//该路由信息是否隐藏的标记
        children:[]
    },

]

const router = new VueRouter({
    routes
})

export default router
